.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#background {
  font-size: 15vh;
  text-align: justify;
  color: transparent;
  text-shadow: 10px 0px 15px rgba(0, 0, 0 ,1);
  line-height: 10vh;
}